import React, {useEffect, useMemo, useState} from 'react';
import { createPortal } from 'react-dom';
import  './FullscreenImage.css';

export default function FullscreenImage( {
    // props
    isFullscreen: isFullscreenProp = false, 
    src, 
    alt, 
    className, 
    style,
}) {
    
    const [isFullscreen, setIsFullscreen] = useState(isFullscreenProp);
    useEffect(() => {
        setIsFullscreen(isFullscreenProp);
    }, [isFullscreenProp]);

    // Den vanliga bilden visas alltid √§ven bakom fullscreen
    // useMemo f√∂r att inte g√∂ra en rerender av den n√§r man g√•r in eller ur fullscreen
    const NonFullscreenImage = useMemo(() =>
        <img src={src} alt={alt}
            className={className}
            style={style}
            onClick={() => setIsFullscreen(!isFullscreen)}
        />
        // React klagar p√• "unnecessary dependencies" men de m√•ste vara med
        , [src, alt, className, style, isFullscreen] 
    )

    // createPortal f√∂r att l√§gga FullscreenImage i toppen av HTML DOM.
    // Kan inte l√§gga den √∂verst med css z-index annars.
    // css stacking context : https://developer.mozilla.org/en-US/docs/Web/CSS/CSS_Positioning/Understanding_z_index/The_stacking_context
    const FullscreenImage = createPortal(
        <div className="fullscreen-container" onClick={() => setIsFullscreen(false)} >
            <img src={src} alt={alt}
                className="fullscreen-image"
                style={style}
            />
        </div>
        , document.getElementById("root")
    )

    return(
        <>
        {/* Den vanliga bilden visas alltid √§ven bakom fullscreen */}
        {NonFullscreenImage}
        
        {isFullscreen &&
            FullscreenImage
        }
        </>
    )
}